import React from 'react';
import { Row, Col } from 'react-bootstrap';
import "./MenuHome.css";
import { useHistory } from "react-router-dom";
import ItemMenu from './ItemMenu';

const MenuHome = () => {
  const users = JSON.parse(localStorage.getItem('users'));
  let history = useHistory();
  const linkMenu = data => {
    history.push({
      pathname: data
    })
  }
  return (
    <div className="menu">
      <Row className="menu-row justify-content-md-center">
        <Col className="cursor-pointer" xs={3} md={3} onClick={() => linkMenu("/contacts")}>
          <ItemMenu linkBadge={`/notifikasi/contact/${users.id}`} linkImage="/media/img/CONTACT ICON.png" />
          <div>CONTACTS</div>
        </Col>
        <Col className="cursor-pointer" xs={3} md={3} onClick={() => linkMenu("/documents")}>
          <ItemMenu linkBadge={`/notifikasi/document/${users.id}`} linkImage="/media/img/DOCUMENT ICON.png" />
          <div>DOCUMENTS</div>
        </Col>
        <Col className="cursor-pointer" xs={3} md={3} onClick={() => linkMenu("/calendar")}>
          <ItemMenu linkBadge={`/notifikasi/calendar/${users.id}`} linkImage="/media/img/CALENDAR ICON.png" />
          <div>CALENDAR</div>
        </Col>
        <Col className="cursor-pointer" xs={3} md={3} onClick={() => linkMenu("/news")}>
          <ItemMenu linkBadge={`/notifikasi/news/${users.id}`} linkImage="/media/img/NEWS ICON.png" />
          <div>NEWS</div>
        </Col>
      </Row>
    </div>
  )
}

export default MenuHome
