import React, { useEffect, useState } from 'react';
import Header from '../../components/Header';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import SaveIcon from '@material-ui/icons/Save';
import { MenuItem, Snackbar} from '@material-ui/core';
import { customAxios } from '../../api/customAxios';
import MuiAlert from '@material-ui/lab/Alert';
import Compressor from 'compressorjs';

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
  root: {
    margin: 'auto',
    maxWidth: 600,
    flexGrow: 1,
    width: '90%'
  },
  circle: {
    position: 'relative',
    margin: 'auto',
    width: '150px',
    height: '150px',
    lineHeight: '150px',
    borderRadius: '50%',
    fontSize: '50px',
    textAlign: 'center',
    backgroundColor: 'grey',
    color: '#000',
    cursor: 'pointer',
  },
  btnCamera: {
    position: 'absolute',
    right: 0,
    bottom: 0,
    width: 50,
    height: 50,
    cursor: 'pointer',
    borderRadius: '50%',
    backgroundColor: '#189a95',
    '&:hover': {
      backgroundColor: '#096763',
      transition: 'background-color 0.5s ease-out',
    },
  },
  flexCamera: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  iconCamera: {
    fontSize: 26,
    margin: 12,
    color: '#FFF'
  },
  name: {
    fontSize: '24px'
  },
  hr: {
    marginBottom: '0 !important',
    marginTop : '0 !important',
    borderTop: '1px solid rgb(0 0 0 / 30%)',
    marginBlockStart: '0 !important',
    marginBlockEnd: '0 !important'
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '5px double #189a95',
    borderRadius: 10,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2),
    display: 'flex',
    flexWrap: 'wrap',
    margin: '0 15px',
    justifyContent: 'center',
  },
  btn: {
    width: '125px',
    align: 'center',
    color: '#189a95',
  }
}));

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText('#189a95'),
    fontSize: '22px',
    backgroundColor: '#189a95',
    '&:hover': {
      backgroundColor: '#15716d',
    },
  },
}))(Button);

export const UserProfil = () => {
  const classes = useStyles();
  const users = JSON.parse(localStorage.getItem('users'));
  const [loadData, setLoadData] = useState(false);
  const [hideHeader, setHideHeader] = useState(false);
  const [dataProfile, setDataProfile] = useState({});
  const [lengthShort, setLengthShort] = useState(0);
  const [alertMessage, setAlertMessage] = useState('');
  const [alertStatus, setAlertStatus] = useState('');
  const [alert, setAlert] = useState(false);
  const [bio, setBio] = useState('');
  const [unit, setUnit] = useState(users.region);
  const [email, setEmail] = useState('');
  const [foto, setFoto] = useState('');
  const [showFoto, setShowFoto] = useState(null);

  // first render
  useEffect(() => {
    const users = JSON.parse(localStorage.getItem('users'));
    setEmail(users.email);
    customAxios(`/profile/${users.email}`)
      .then(res => {
        setDataProfile(res.data.data);
        setBio(res.data.data.bio);
        setLengthShort(res.data.data.bio.length);
        setUnit(res.data.data2.unit);
        if (res.data.data.foto){
          setShowFoto(process.env.REACT_APP_UPLOADS + 'profile/' + encodeURI(res.data.data.foto));
        }
      })
      .catch(err => console.log(err.message) )
  }, [])

  useEffect(() => {
    if (loadData){
      const users = JSON.parse(localStorage.getItem('users'));
      customAxios(`/profile/${users.email}`);
      setTimeout(() => window.location.reload(), 3000);
    }
  }, [loadData])

  const handleClose = (event, reason) => {
    window.location.reload();
    if (reason === 'clickaway') {
      return;
    }
    setAlert(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const formData = new FormData();
    formData.append("foto", foto);
    formData.append("email", email);
    formData.append("bio", bio);
    formData.append("unit", unit);

    // check if online or offline
    if ('serviceWorker' in navigator) {
      var condition = navigator.onLine ? "online" : "offline";
      if(condition === "online"){
        customAxios({
          url: '/profile/change',
          method: 'post',
          data: formData,
          headers: {
            'content-type': 'multipart/form-data'
          }
        })
          .then(res => {
            if (res.status < 300) {
              setLoadData(true);
              setHideHeader(true);
              setAlert(true);
              setAlertStatus('success');
              setAlertMessage('Data User Profile updated successfully!')
            }else{
              setAlert(true);
              setAlertStatus('error');
              setAlertMessage('Something went wrong on the server!')
            }
          })
          .catch(err => {
            setAlert(true);
            setAlertStatus('error');
            setAlertMessage('Something went wrong on the server!')
          })
      }else{
        localStorage.setItem("updateUserProfile", JSON.stringify({ foto, email, bio, unit}));
        setAlert(true);
        setAlertStatus('error');
        setAlertMessage(`You're offline, the data is updated immediately if you are online!`)
      }
    }

  }

  return (
    <>
    <Snackbar open={alert} autoHideDuration={3000} onClose={handleClose}>
      <Alert onClose={handleClose} severity={alertStatus}>
        {alertMessage}
      </Alert>
    </Snackbar>
    <Header link={hideHeader ? null : '/settings'} />
    <div className={classes.root}>
      <form onSubmit={handleSubmit}>
        <h5 className="text-center my-4">
          USER PROFILE
        </h5>
        <input
          hidden
          name="foto"
          onChange={(e) => {
            new Compressor(e.target.files[0], {      
              quality: 0.8,
              success: (compressedResult) => {
                setShowFoto(URL.createObjectURL(compressedResult));
                setFoto(compressedResult);
              },
            });
          }}
          accept="image/*"
          id="upload-foto"
          type="file"
        />
        <div className={classes.circle + " mb-3"}>
          <label htmlFor="upload-foto">
            <div className={classes.btnCamera}>
              <div className={classes.flexCamera}>
                <CameraAltIcon className={classes.iconCamera} />
              </div>
            </div>
          </label>
          <img
            src={showFoto ?? process.env.PUBLIC_URL + '/media/img/default-user.jpeg'}
            alt={dataProfile.first_name + ' ' + dataProfile.last_name}
            style={{ width: '100%', height: '100%', zIndex: 20, borderRadius: '50%', verticalAlign: 'initial' }}
            />
        </div>
        <TextField 
          className="mb-2" 
          label="Short Bio" 
          name="bio"
          value={bio}
          multiline
          rows={3}
          fullWidth
          inputProps={{ maxLength: 150 }}
          onChange={(e) => {
            setLengthShort(e.target.value.length);
            setBio(e.target.value);
          }}
          helperText={`${lengthShort} of 150 characters`}
        />
        <TextField
          id="standard-select-currency"
          select
          label="Select Unit"
          value={unit}
          name="unit"
          onChange={(e) => setUnit(e.target.value)}
          fullWidth
        >
          <MenuItem value="AP">Asia Pacific</MenuItem>
          <MenuItem value="AME">Africa and the middle east</MenuItem>
          <MenuItem value="LACC">Latin America, Canada and the Carribean</MenuItem>
          <MenuItem value="REE">Russia, Europe, and Eurasia</MenuItem>
          <MenuItem value="DO">Directors Office</MenuItem>
          <MenuItem value="DASP">DASP</MenuItem>
          <MenuItem value="GP">Global Programs</MenuItem>
          <MenuItem value="MS">Migratory Species</MenuItem>
          <MenuItem value="MSU">Mission Support</MenuItem>
          <MenuItem value="PO">Policy</MenuItem>
          <MenuItem value="UOP">Urban Outreach and Partnerships</MenuItem>
          <MenuItem value="WISC">Wood Identification and Screening Center</MenuItem>
        </TextField>
        <ColorButton type="submit" className="mt-5" fullWidth variant="contained" color="primary" startIcon={<SaveIcon />}>
          SAVE DATA
        </ColorButton>
      </form>
    </div>
  </>
  )
}
